import Swiper, { Navigation, Scrollbar } from 'swiper';
Swiper.use([Navigation, Scrollbar]);
const swiper = new Swiper('.quality__slider', {
  slidesPerView: 'auto',
  // loop: true,
  grabCursor: true,
  spaceBetween: 40,
  scrollbar: {
    el: '.quality__scroll',
    draggable: true,
  },
  navigation: {
    nextEl: '.quality__slider-btn--next',
    prevEl: '.quality__slider-btn--prev',
  },
});
